@import url(https://fonts.googleapis.com/css2?family=Jost:wght@400;500&family=Rowdies&display=swap&family=Inconsolata);
* {
  margin: 0;
  font-family: "Jost", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
}

code {
  font-family: proxima-nova, Helvetica, Arial, sans-serif;
}

body {
  width: 100%;
  margin: 0;
  padding: 0;
  height: 100%;
}

@media screen and (min-width: 601px) {
  body {
    padding: 2.5em;
  }
}


.Project-container {
  padding: 90px 0 400px 0;
}

.Project-container:last-of-type {
  margin-bottom: 100px;
}

.Project-img-container {
  position: absolute;
}

.Project-img {
  width: 480px;
  position: absolute;
  border-radius: 4px;
  box-shadow: 0 10px 25px 2px grey;
}

.Project-img--left {
  left: 600px;
}

.Project-board {
  margin-top: 50px;
  width: 480px;
  padding: 25px 0;
  border-radius: 3px;
  font-size: 15px;
  z-index: 2;
}

.Project-board--left {
  position: absolute;
  text-align: left;
}

.Project-board--right {
  position: absolute;
  text-align: right;
  left: 880px;
}

.Project-text {
  padding: 10px 0;
}

.Project-header {
  font-size: 20px;
  font-family: "Rowdies", cursive;
}

.Project-icon {
  margin-top: 15px;
  cursor: pointer;
}

.Project-icon:hover {
  cursor: pointer;
}

.Language-list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0px;
  margin: 15px 0 10px;
}

.Language-list-elem {
  margin: 3px 10px 3px 0;
  font-family: Inconsolata, monospace;
  font-size: 0.75rem;
  font-weight: 600;
  padding: 2px 10px;
  border-radius: 3px;
  color: #007bff;
  border: 1px solid #007bff;
  background: 0 0;
}

.Header-text {
  font-size: xx-large;
  font-family: "Rowdies", cursive;
}

.Header-container {
  top: 25px;
  left: 36vw;
  text-align: center;
}

.Project-all-container {
  display: block;
  padding: 0 15%;
  margin: 0;
}

.Navbar-all-container a {
  text-decoration: none;
  color: #767676;
}

.Navbar-all-container {
  position: fixed;
  top: 17px;
  left: 85vw;
  max-width: 15vw;
  max-height: 70vh;
  overflow: auto;

  margin-top: 23px;
  margin-bottom: 20px;
  padding-left: 10px;
  padding-right: 10px;

  list-style: none;
  list-style-position: outside;
}

.Navbar-container {
  max-height: calc(70vh - 100px);
  overflow: hidden;
}

.Navbar {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.Navbar > li {
  padding: 3px;
}

.Navbar > li > a {
  display: block;
  padding: 4px 18px;
  font-size: 14px;
  font-weight: 500;

  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre;
}

.Navbar .Navbar-link--active a {
  font-weight: 700;
  color: #000;
  background-color: transparent;
}

.Navbar-subheading-container > li {
  list-style: none;
  padding: 4px 0;
}

.Navbar-subheading-container > li > a {
  font-weight: 500;
  font-size: 14px;
  padding-top: 1px;
  padding-bottom: 1px;
}

.Top-button {
  display: inline-block;
  transition: all 0.2s ease-in-out;
  background: 0 0;
  border: none;
  outline: 0;
  width: 40px;
  position: fixed;
  bottom: 25px;
  right: 30px;
  cursor: pointer;
  z-index: 3;
}

.Top-button img {
  width: 100%;
}

.Footer {
  line-height: 1;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  height: auto;
  min-height: 70px;
  padding: 15px;
}

.Footer-credit {
  font-weight: 600;
  letter-spacing: 0.025em;
  font-family: Inconsolata, monospace;
  color: rgb(16, 162, 245);
  padding: 10px;
  text-decoration: none;
}
.Footer-credit:visited {
  color: rgb(168, 178, 209);
}

.Footer-credit:hover {
  cursor: pointer;
  color: rgb(16, 162, 245);
}

.Writing-archive-container {
  margin: 40px 0 50px 850px;
  left: 850px;
}

.Writing-line--title {
  font-weight: 500;
  text-decoration: none;
  font-size: 1rem;
  color: inherit;
}
.Writing-line-container {
  padding: 5px;
}

.Writing-line--publish {
  color: rgb(168, 178, 209);
  font-size: 0.9rem;
  margin: 5px 0;
}

.Writing-page-container {
  margin: auto;
  width: 100%;
  display: block;
}

.Writing-page-content {
  position: relative;
}

.Writing-thought-container {
  position: absolute;
  left: 250px;
}

.Writing-thought-header {
  font-size: 28px;
  font-family: "Rowdies", cursive;
  margin-bottom: 10px;
}

.DesignNotes--thoughts {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  text-align: justify;
  padding: 40px 30%;
}

.DesignNotes--thoughts p {
  font-size: 17px;
}

.Girl-container {
  position: relative;
}

.Girl-diva {
  height: 80vh;
  padding-top: 5%;
  padding-left: 20%;
  padding-bottom: 10%;
  z-index: 20;
}

.DisplayText {
  font-size: xx-large;
  font-family: "Rowdies", cursive;
}

.Welcome-container {
  position: absolute;
  left: 45%;
  top: 35%;
  pointer-events: none;
  color: #000;
  letter-spacing: 2px;
  line-height: 1.5;
}

